@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

@mixin hover {
  @media (hover: hover) {
    &:hover:not(.focus-visible) {
      @content;
    }
  }
}

@mixin focus {
  &.focus-visible:focus {
    @content;
  }
}

@mixin active {
  @media (hover: hover) {
    &:active:not(.focus-visible) {
      @content;
    }
  }
}

@mixin hover-focus {
  @media (hover: hover) {
    &:hover:not(.focus-visible) {
      @content;
    }
  }

  &.focus-visible:focus {
    @content;
  }
}

// Desktop first

@mixin vp-1920 {
  @media (max-width: ($vp-1920)) {
    @content;
  }
}

@mixin vp-1600 {
  @media (max-width: ($vp-1600)) {
    @content;
  }
}

@mixin vp-1440 {
  @media (max-width: ($vp-1440)) {
