@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "OpenSans";
  font-display: swap;
  src: url("../fonts/OpenSans-Medium.woff2") format("woff2"), url("../fonts/OpenSans-Medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "OpenSans";
  font-display: swap;
  src: url("../fonts/OpenSans-Regular.woff2") format("woff2"), url("../fonts/OpenSans-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "OpenSans";
  font-display: swap;
  src: url("../fonts/OpenSans-SemiBold.woff2") format("woff2"), url("../fonts/OpenSans-SemiBold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "OpenSans", "Arial", sans-serif;
  color: #292929;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  background-color: black;
}

a {
  color: #292929;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #292929;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.btn-reset {
  padding: 0;
  border: none;
  background-color: rgba(255, 255, 255, 0);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1600px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1024px) {
  .container {
    padding: 0 30px;
  }
}
@media (max-width: 768px) {
  .container {
    padding: 0 20px;
  }
}

.header {
  z-index: 11;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  background-color: #ffffff;
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0 20px;
}
.header__logo {
  position: relative;
  display: block;
  width: 99px;
  height: 44px;
}
.header__logo svg {
  display: block;
  width: 99px;
  height: 44px;
}

.main-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1421px;
  /* &.is-active {
    .main-nav__toggle {
      &::before {
        top: 15px;
        transform: rotate(-45deg) translate(0, -50%);
      }

      &::after {
        top: 13px;
        transform: rotate(45deg) translate(0, 50%);
      }
    }

    .main-nav__list {
      transform: translateX(0);
    }
  } */
}
.main-nav__toggle {
  z-index: 11;
  position: relative;
  display: none;
  width: 30px;
  height: 30px;
  cursor: pointer;
  user-select: none;
  transition: opacity 0.3s ease;
}
@media (max-width: 768px) {
  .main-nav__toggle {
    display: block;
  }
}
.main-nav__toggle::before, .main-nav__toggle::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #ffffff;
  transition: transform 0.3s ease;
}
.main-nav__toggle::before {
  top: 10px;
}
.main-nav__toggle::after {
  bottom: 10px;
}
@media (hover: hover) {
  .main-nav__toggle:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.main-nav__toggle.focus-visible:focus {
  opacity: 0.8;
}
.main-nav__list {
  display: flex;
  margin: 0 20px 0 73px;
  padding: 18px 0 6px;
  list-style: none;
  /* @include vp-768 {
    z-index: 10;
    position: fixed;
    flex-direction: column;
    justify-content: flex-start;

    top: 100px;
    left: 0;

    width: 100%;
    height: 100vh;
    padding: 100px 20px 20px;

    background-color: rgba($color-black-russian, 0.9);

    transition: transform $trans-default;
    transform: translateX(100%);
  } */
}
.main-nav__item {
  margin-right: 40px;
}
.main-nav__item:last-child {
  margin-right: 0;
}
.main-nav__link {
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  color: #292929;
  transition: opacity 0.3s ease;
  transform: translate3d(0, 0, 0);
}
.main-nav__link svg {
  flex-shrink: 0;
  width: 11px;
  height: 6px;
  margin-left: 5px;
  transition: transform 0.3s ease;
}
@media (hover: hover) {
  .main-nav__link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.main-nav__link.focus-visible:focus {
  opacity: 0.8;
}
.main-nav__dropdown {
  display: none;
}
.main-nav__sublist {
  margin: 0;
  padding: 20px;
  border-radius: 0 0 4px 4px;
  background-color: #f1f1f1;
  list-style: none;
}
.main-nav__subitem {
  margin-bottom: 15px;
}
.main-nav__subitem:last-child {
  margin-bottom: 0;
}
.main-nav__sublink {
  display: block;
  font-size: 15px;
  line-height: 20px;
  color: #292929;
}
.main-nav__user-nav {
  display: flex;
  align-items: center;
  padding: 11px 0 0;
}
.main-nav__search {
  margin-right: 50px;
}
.main-nav__account {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 20px;
  cursor: pointer;
  user-select: none;
}
.main-nav__account svg {
  flex-shrink: 0;
  width: 22px;
  height: 22px;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .main-nav__account:hover:not(.focus-visible) svg {
    opacity: 0.8;
  }
}
.main-nav__account.focus-visible:focus svg {
  opacity: 0.8;
}
.main-nav__language {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25px;
  height: 25px;
  margin-right: 20px;
  border: 1px solid #292929;
  border-radius: 50%;
  transition: opacity 0.3s ease;
  cursor: pointer;
  user-select: none;
}
.main-nav__language span {
  display: block;
  font-size: 12px;
  line-height: 16px;
  color: #292929;
}
@media (hover: hover) {
  .main-nav__language:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.main-nav__language.focus-visible:focus {
  opacity: 0.8;
}
.main-nav__basket {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  user-select: none;
}
.main-nav__basket svg {
  flex-shrink: 0;
  width: 21px;
  height: 21px;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .main-nav__basket:hover:not(.focus-visible) svg {
    opacity: 0.8;
  }
}
.main-nav__basket.focus-visible:focus svg {
  opacity: 0.8;
}

.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  padding: 14px 9px;
  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
  text-transform: uppercase;
  border: 1px solid #ffffff;
  border-radius: 4px;
  background-color: #3e9cdc;
  color: #ffffff;
  cursor: pointer;
  user-select: none;
  transition: opacity 0.3s ease;
  outline: none;
}
.btn--white {
  border: 1px solid #6f6d7b;
  background-color: #ffffff;
  color: #2a2b30;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.search {
  width: 117px;
  padding: 6px 0;
  border: 1px solid #292929;
  border-radius: 3px;
}
.search__icon {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 10px;
}
.search__icon svg {
  flex-shrink: 0;
  width: 12px;
  height: 13px;
}
.search label {
  display: flex;
  align-items: center;
  margin: 0 -1px;
  padding: 0 10px;
  border-radius: 2px;
  background-color: #ffffff;
}
.search input {
  width: 100%;
  font-weight: 300;
  font-size: 14px;
  line-height: 19px;
  border: none;
  color: #292929;
  outline: none;
}
.search input::placeholder {
  font-weight: 300;
  font-size: 14px;
  line-height: 19px;
  color: #838383;
}